import React, { useState, useRef, useCallback } from "react";
import JoditEditor from "jodit-react";

export const TextEditor = React.memo(() => {
  const [content, setContent] = useState("");
  const [showContent, setShowContent] = useState(false);
  const editorRef = useRef(null);
  const timeoutRef = useRef<any>(null); // Store timeout ID

  const config = {
    height: 500,
    showCharsCounter: false,
    showWordsCounter: false,
    showXPathInStatusbar: false,
    placeholder: "Type your content here...",
    readonly: false,
    buttons:
      "bold,italic,underline,strikethrough,ul,ol,list,font,fontsize,paragraph,lineHeight,superscript,subscript,classSpan,spellcheck,speechRecognize,paste,hr,symbols,indent,preview",
  };

  // Extract text from HTML
  const extractTextContent = (html: any) => {
    // const tempDiv = document.createElement("div");
    // tempDiv.innerHTML = html;
    // return tempDiv.textContent || tempDiv.innerText || "";
    return html;
  };

  // Debounced onChange handler
  const handleContentChange = useCallback((newContent: string) => {
    if (timeoutRef.current) clearTimeout(timeoutRef.current);
    
    timeoutRef.current = setTimeout(() => {
      setContent(newContent); // Update state after 500ms delay
    }, 500);
  }, []);

  const handleButtonClick = () => {
    // console.log(extractTextContent(content).toLowerCase());
    console.log(extractTextContent(content));
    setShowContent(true);
  };

  return (
    <div className="w-full mx-auto">
      <JoditEditor
        ref={editorRef}
        value={content}
        config={config}
        onChange={handleContentChange} // ✅ Debounced update
      />
      <button
        onClick={handleButtonClick}
        className="bg-blue-500 text-white px-4 py-1 rounded-md mt-4"
      >
        Submit
      </button>

      {showContent && (
        <div className="mt-4 p-4 border border-gray-300 rounded-md prose">
          <div dangerouslySetInnerHTML={{ __html: content }} />
        </div>
      )}
    </div>
  );
});
